import { createSlice } from "@reduxjs/toolkit";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { v4 as uuidv4 } from "uuid";

const initialState = {
  currentUser: null,
  loading: false,
  error: null,
};

const userSlice = createSlice({
  name: "user",
  initialState,
  reducers: {
    loginUserStart(state) {
      state.loading = true;
      state.error = null;
    },
    loginUserSuccess(state, action) {
      state.loading = false;
      state.currentUser = action.payload;
      toast.success("Login bem-sucedido!");
    },
    loginUserFailure(state, action) {
      state.loading = false;
      state.error = action.payload;
      toast.error("Erro ao fazer login. Credenciais incorretas.");
    },
    logout(state) {
      state.currentUser = null;
      state.loading = false;
      state.error = null;
    },
  },
});

export const { loginUserStart, loginUserSuccess, loginUserFailure, logout } =
  userSlice.actions;

// Async Thunk que listena os estados e atualiza
export const loginUser =
  (username, password, onSuccess, onError) => async (dispatch) => {
    dispatch(loginUserStart());
    try {
      const user = await authenticate(username, password);
      dispatch(loginUserSuccess(user));
      onSuccess(user);
    } catch (error) {
      dispatch(
        loginUserFailure("Erro ao fazer login. Credenciais incorretas.")
      ); // Erro durante o login
      toast.error("Erro ao fazer login. Credenciais incorretas.");
      onError(error);
    }
  };

//Funcao simulada
let authenticate = (username, password) => {
  return new Promise((resolve, reject) => {
    setTimeout(() => {
      if (username === "fasdfadfaf@adfjadfjadsl" && password === "123456789") {
        resolve({
          id: `${uuidv4()}`,
          username: "Tiago",
          username,
          password
        });
      } else {
        reject(new Error("Credenciais incorretas"));
      }
    }, 1000); // espera 1 segundo antes de resolver a Promise
  });
};

export default userSlice.reducer;
