import React from "react";
import LogoERES from "../../assets/images/eres-pequeno.png";
import { Sidebar, Logo, SideMenu, MenuItem, MenuLink } from "./SidebarStyle"; // Ajuste o caminho de importao conforme necessrio
import ThemeToggle from "../../components/ToggleTheme/ToggleTheme";
import { Link } from "react-router-dom";

const MySidebar = ({ estadoCompartilhado }) => {
  return (
    <Sidebar isOpen={estadoCompartilhado}>
      <Logo href="#">
        <img src={LogoERES} alt="Logo" />
        <div className="logo-name">
          <span>Logstica</span>
        </div>
      </Logo>
      <SideMenu isOpen>
        <MenuItem className="active">
          <Link to="/home">
            <MenuLink href="#">
              <i className="bx bxs-dashboard"></i>Dashboard
            </MenuLink>
          </Link>
        </MenuItem>
        <MenuItem>
          <Link to='/produtos' >
            <MenuLink href="#">
              <i className="bx bx-store-alt"></i>Produtos
            </MenuLink>
          </Link>
        </MenuItem>
        <MenuItem>
          <Link to='/vendedores'>
            <MenuLink href="#">
              <i className="bx bx-group"></i>Vendedores
            </MenuLink>
          </Link>
        </MenuItem>
        <MenuItem>
          <Link to='/configuracoes' >
          <MenuLink href="#">
            <i className="bx bx-cog"></i>Configuraes
          </MenuLink>
          </Link>
        </MenuItem>
      </SideMenu>
      <SideMenu>
        <MenuItem>
          <ThemeToggle></ThemeToggle>
          <MenuLink>Tema</MenuLink>
        </MenuItem>
        <MenuItem>
          <MenuLink href="#" className="logout">
            <i className="bx bx-log-out-circle"></i>
            Logout
          </MenuLink>
        </MenuItem>
      </SideMenu>
    </Sidebar>
  );
};

export default MySidebar;
