import React, { useState } from "react";
import EresLogoG from "../../assets/images/eres-g.png";
import { useNavigate } from "react-router-dom";
import "./Login.css";
import { useDispatch } from "react-redux";
import { loginUser } from "../../redux/user/userSlice";
import { toast, ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const Login = () => {
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [emailError, setEmailError] = useState("");
  const [passwordError, setPasswordError] = useState("");
  const [toastShown, setToastShown] = useState(false); // Flag de estado para controlar se o toast j foi exibido
  const navigate = useNavigate();
  const dispatch = useDispatch();

  const handleEmailChange = (event) => {
    setEmail(event.target.value);
    if (event.target.value.length >= 8) {
      setEmailError("");
    } else {
      setEmailError("O email deve ter pelo menos 8 caracteres.");
    }
  };

  const handlePasswordChange = (event) => {
    setPassword(event.target.value);
    if (event.target.value.length >= 5) {
      setPasswordError("");
    } else {
      setPasswordError("A senha deve ter pelo menos 5 caracteres.");
    }
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    if (email.length >= 8 && password.length >= 5 && !toastShown) {
      // Verifica se o toast j foi exibido antes de exibi-lo novamente
      dispatch(
        loginUser(
          email,
          password,
          (user) => {
            navigate("/home");
          },
          (error) => {
            toast.error("Credenciais invlidas. Por favor, tente novamente.");
            setToastShown(true); // Define a flag de estado para indicar que o toast foi exibido
          }
        )
      );
    }
  };

  return (
    <div className="principal">
      <div className="container" id="container">
        <ToastContainer />
        <div className="form-container sign-in">
          <form className="login_form" onSubmit={handleSubmit}>
            <br />
            <img
              className="image-panel"
              src={EresLogoG}
              alt="Logomarca da Eres"
            />
            <h1>Faa seu login</h1>
            <div className="social-icons">{/* cones de redes sociais */}</div>
            <span>Informe seus dados</span>

            <input
              name="email"
              id="campo_email"
              type="email"
              placeholder="Email"
              value={email}
              onChange={handleEmailChange}
            />
            {emailError && <div className="error-message">{emailError}</div>}

            <input
              name="password"
              id="campo_senha"
              type="password"
              placeholder="Senha"
              value={password}
              onChange={handlePasswordChange}
            />
            {passwordError && (
              <div className="error-message">{passwordError}</div>
            )}

            <a href="#">Esqueceu sua senha?</a>
            <br />
            <button type="submit">Logar</button>
          </form>
        </div>
        <div className="toggle-container">
          <div className="toggle">
            <div className="toggle-panel toggle-right">
              <img
                className="image-panel"
                src={EresLogoG}
                alt="Logomarca da eres"
                width="250"
                height="150"
              />
              <div className="conteudo-pane-right">
                <br />
                <h1>Geo ERES LTDA</h1>
                <p>Faa login para acessar o painel de localizao!</p>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
};

export default Login;
