import React from "react";
import "./Vendedores.Style.jsx";
import Vend1 from "../../../assets/images/customer01.jpg";
import Vend2 from "../../../assets/images/customer02.jpg";
import { RecentCustomersContainer } from "./Vendedores.Style.jsx";

const Vendedores = () => {
  return (
    <RecentCustomersContainer>
      <div className="cardHeader">
        <h2>Vendedores</h2>
      </div>

      <table>
        <tbody>
          <tr>
            <td width="60px">
              <div className="imgBx">
                <img src={Vend1} alt="" />
              </div>
            </td>
            <td>
              <h4>
                Sami <br /> <span>Jua</span>
              </h4>
            </td>
          </tr>
          <tr>
            <td width="60px">
              <div className="imgBx">
                <img src={Vend2} alt="" />
              </div>
            </td>
            <td>
              <h4>
                Ribamar <br /> <span>Crato</span>
              </h4>
            </td>
          </tr>
        </tbody>
      </table>
    </RecentCustomersContainer>
  );
};

export default Vendedores;
