// export default CardsTopo;
import React from 'react';
import './CardsTopoStyle.jsx';
import { InsightItem, InsightsContainer } from './CardsTopoStyle.jsx';

const CardsTopo = ({ cards }) => {
  return (
    <InsightsContainer>
      {cards.map((item, index) => (
        <InsightItem key={index}>
          <i className={`bx ${item.icon}`}></i>
          <span className="info">
            <h3>{item.value}</h3>
            <p>{item.label}</p>
          </span>
        </InsightItem>
      ))}
    </InsightsContainer>
  );
};

export default CardsTopo;
