import { Fragment, useState } from "react";
import {
  GoogleMap,
  InfoWindowF,
  MarkerF,
  useLoadScript,
} from "@react-google-maps/api";


const markers = [
  {
    id: 1,
    name: "Sami",
    position: { lat: -7.208244900078705, lng: -39.311138191088865 }
  },
  {
    id: 2,
    name: "Ribamar",
    position: { lat: -7.2023525041958125, lng: -39.31131562832077 },
  },
  {
    id: 3,
    name: "Tiago",
    position: { lat: -7.206322158451565, lng: -39.303176491088735 },
  }
];

function MapApi() {
  const { isLoaded } = useLoadScript({
    //TODO: Refatorar par um arquivo .env
    googleMapsApiKey: `AIzaSyDASrPRxQwn3E5AMaTIRntfWuvMSv5BL-U`,
  });

  const [activeMarker, setActiveMarker] = useState(null);

  const handleActiveMarker = (marker) => {
    if (marker === activeMarker) {
      return;
    }
    setActiveMarker(marker);
  };

  return (
    <Fragment>
      {isLoaded ? (
        <GoogleMap
          //Posicao inicial
          center={{ lat: -7.205128254367043, lng: -39.311278810244076 }}
          zoom={10}
          onClick={() => setActiveMarker(null)}
          mapContainerStyle={{ width: "100%", height: "50vh" }}
        >
          {markers.map(({ id, name, position }) => (
            <MarkerF
              key={id}
              position={position}
              onClick={() => handleActiveMarker(id)}
            //   icon={{
            //     url:"https://t4.ftcdn.net/jpg/02/85/33/21/360_F_285332150_qyJdRevcRDaqVluZrUp8ee4H2KezU9CA.jpg",
            //     scaledSize: { width: 50, height: 50 }
            //   }}
            >
              {activeMarker === id ? (
                <InfoWindowF onCloseClick={() => setActiveMarker(null)}>
                  <div>
                    <p>{name}</p>
                  </div>
                </InfoWindowF>
              ) : null}
            </MarkerF>
          ))}
        </GoogleMap>
      ) : null}
    </Fragment>
  );
}

export default MapApi;