import React from 'react';
import LocalPage from "../../components/Main/LocalPage/LocalPage";
import CardsTopo from "../../components/Main/CardsTop/CardsTopo";
import Mapa from "../../components/Main/Mapa/Mapa";
import Vendedores from "../../components/Main/Vendedores/Vendedores";
import ContainerMapVend from "../../components/Main/ContainerMapVend/ContainerMapVend";

const Home = () => {

  const cards = [
    { icon: 'bx-calendar-check', value: '533', label: 'Vendidos hoje' },
    { icon: 'bx-show-alt', value: '5', label: 'Monitorados' },
    { icon: 'bx-line-chart', value: 'R$ 50.000,00', label: 'Meta do dia' },
    { icon: 'bx-dollar-circle', value: 'R$ 32.433,99', label: 'Meta atingida' },
    { icon: 'bx-dollar', value: 'R$ 32.433,99', label: 'Flex do dia' },
  ];

  return (
    <>
      <LocalPage  pontoPartida={'DashBoard'}/>
      <CardsTopo cards={cards} />
      <ContainerMapVend>
        <Mapa />
        <Vendedores />
      </ContainerMapVend>
    </>
  );
};

export default Home;