import styled from 'styled-components';

export const InsightsContainer = styled.div`
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(240px, 1fr));
  grid-gap: 24px;
  margin-top: 36px;
`;

export const InsightItem = styled.li`
  padding: 24px;
  background: ${({ theme }) => theme.light};
  border-radius: 20px;
  display: flex;
  align-items: center;
  gap: 24px;
  cursor: pointer;

  .bx {
    width: 60px;
    height: 60px;
    border-radius: 10px;
    font-size: 36px;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  &:nth-child(1) .bx {
    background: ${({ theme }) => theme.lightPrimary};
    color: ${({ theme }) => theme.rederes};
  }

  &:nth-child(2) .bx {
    background: ${({ theme }) => theme.lightWarning};
    color: ${({ theme }) => theme.rederes};
  }

  &:nth-child(3) .bx {
    background: ${({ theme }) => theme.lightSuccess};
    color: ${({ theme }) => theme.reders};
  }

  &:nth-child(4) .bx {
    background: ${({ theme }) => theme.lightDanger};
    color: ${({ theme }) => theme.rederes};
  }

  &:nth-child(5) .bx {
    background: ${({ theme }) => theme.lightPrimary};
    color: ${({ theme }) => theme.rederes};
  }

  .info h3 {
    font-size: 20px;
    font-weight: 600;
    color: ${({ theme }) => theme.dark};
  }

  .info p {
    color: ${({ theme }) => theme.dark};
  }
`;
