import styled from 'styled-components';

export const RecentCustomersContainer = styled.div`
  position: relative;
  min-height: 500px;
  padding: 20px;
  background: ${({ theme }) => theme.light};
  box-shadow: 0 7px 25px rgba(0, 0, 0, 0.08);
  border-radius: 20px;
  color: ${({ theme }) => theme.dark};

  .imgBx {
    position: relative;
    width: 40px;
    height: 40px;
    border-radius: 50px;
    overflow: hidden;

    img {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      object-fit: cover;
    }
  }

  table tr td {
    padding: 12px 10px;

    h4 {
      font-size: 16px;
      font-weight: 500;
      line-height: 1.2rem;

      span {
        font-size: 14px;
        color: ${({ theme }) => theme.secondary};
      }
    }
  }

  table tr:hover {
    background: ${({ theme }) => theme.secondary};
    color: ${({ theme }) => theme.white};

    td h4 span {
      color: ${({ theme }) => theme.white};
    }
  }
`;
