import { Navigate, Outlet } from 'react-router-dom';

const ProtetorRotas = ({ isAuthenticated }) => {
  // Se o usurio no est autenticado, redirecione para a pgina de login.
  if (!isAuthenticated) {
    return <Navigate to="/naoAutorizado" />;
  }

  // Se o usurio est autenticado, permita o acesso  rota.
  return <Outlet />;
};

export default ProtetorRotas;